
function calculateAmortizationTable(h) {
    let n = getAnnos(h)
    let	i = getInterest(h)
    let	c = calculateCuota(h)
    let	k = h.C
    let [j, kp, ip, kt, it, d] = [0, 0, 0, 0, 0, 0];
    let tabla = [];

    while (n > 0) {
        n -= 1;
        j += 1;

        if (n > 0)
        {
            kp = c - i * k;
            ip = i * k;
            kt += kp;
            it += ip;
            d = (1 + i) * k - c;
            AddToTabla(tabla, j, h.i, c, kp, ip, kt, d, it);
        }
        else
        {
            AddToTabla(tabla, j, h.i, k + i*k, k, i * k, kt+k, k-d, it+i*k);
        }
        k = d;
    }
    return tabla;
}

function calculateCuota(h) {
    return (h.C * getInterest(h) / (1 - Math.pow(1 + getInterest(h), -1 * getAnnos(h))));
}

function getInterest(h) {
    return h.i / 1200;
}

function getAnnos(h) {
    return h.n * 12;
}


function AddToTabla(h,j,interes,c,k,i,kt,kd,it) {
    h.push({
        periodo : j,
        interes: interes,
        cuota: c,
        capitalPeriodo : k,
        interesPeriodo : i,
        capitalTotal: kt,
        capitalPendiente: kd,
        interesTotal: it
    })
}

export {
    calculateAmortizationTable,
    calculateCuota
}


